
#canvas {
  display: block;
}
section {
  position: fixed;
  top: 40px;
  left: 20%;
  width: 60%;
  min-height: 80px;
  padding: 20px;
  border: 2px solid white;
  text-indent: 2em;
  font-size: 24px;
  color: white;
  overflow: auto;
  word-break: break-all;
}
html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}
html {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-user-select: none;
  -moz-user-focus: none;
  -ms-user-select: none;
  -moz-user-select: none;
       user-select: none;
}
